import LineGradient from "./LineGradiant";
import { motion } from "framer-motion";
import projectone from '../assets/project-1.jpeg';
import projecttwo from '../assets/project-2.jpeg';
import projectthree from '../assets/project-3.jpeg';
import projectfour from '../assets/project-4.jpeg';
import projectfive from '../assets/project-5.jpeg';
import projectsix from '../assets/project-6.jpeg';
import projectseven from '../assets/project-7.jpeg';


const container = {
  hidden: {},
  visible: {
    transition: {
      staggerChildren: 0.4,
    },
  },
};

const projectVariant = {
  hidden: { opacity: 0, scale: 0.8 },
  visible: { opacity: 1, scale: 1 },
};


const Project = ({ src,title,words }) => {
  const overlayStyles = `absolute h-full w-full opacity-0 hover:opacity-90 transition duration-500
    bg-grey z-30 flex flex-col justify-center items-center text-center p-16 text-deep-blue`;
  const projectTitle = title.split(" ").join("-").toLowerCase();
  console.log(src)

  return (
    <motion.div variants={projectVariant} className="relative">
      <div className={overlayStyles}>
        <p className="text-2xl font-playfair">{title}</p>
        <p className="mt-7">
          {words}
        </p>
      </div>
      <img src={src} alt={projectTitle} />
    </motion.div>
  );
};

const Projects = () => {
  return (
    <section id="project" className="pt-40 pb-30">
      {/* HEADINGS */}
      <motion.div
        className="md:w-2/5 mx-auto text-center"
        initial="hidden"
        whileInView="visible"
        viewport={{ fallback:true, amount: 0.5 }}
        transition={{ duration: 0.5 }}
        variants={{
          hidden: { opacity: 0, y: -50 },
          visible: { opacity: 1, y: 0 },
        }}
      >
        <div>
          <p className="font-playfair font-semibold text-4xl">
            <span className="text-yellow">PRO</span>JECTS
          </p>
          <div className="flex justify-center mt-5">
            <LineGradient width="w-2/3" />
          </div>
        </div>
        <p className="mt-10 mb-10">
          Aliquam, amet dui feugiat facilisi dui. Aliquam aliquet integer ut
          fames odio in at. At magna ornare dictum lectus. Purus massa morbi
          purus nec eget eleifend ut elit.
        </p>
      </motion.div>

      {/* PROJECTS */}
      <div className="flex justify-center">
        <motion.div
          className="sm:grid sm:grid-cols-3"
          variants={container}
          initial="hidden"
          whileInView="visible"
          viewport={{ fallback:true, amount: 0.2 }}
        >
          {/* ROW 1 */}
          <div
            className="flex justify-center text-center items-center p-10 bg-yellow
              max-w-[400px] max-h-[400px] text-2xl font-playfair font-semibold"
          >
            BEAUTIFUL USER INTERFACES
          </div>
          <Project title="Project 1" src={projectone} words="In this project I am build this application"/>
          <Project title="Project 2" src={projecttwo} words="In this project i am build the web for practice"/>

          {/* ROW 2 */}
          <Project title="Project 3" src={projectthree} words="In this project i am try to to something different for login page"/>
          <Project title="Project 4" src={projectfour} words="In this project i am try to to something different for login page"/>
          <Project title="Project 5" src={projectfive} words="In this project i am try to to something different for login page"/>

          {/* ROW 3 */}
          <Project title="Project 6" src={projectsix} words="In this project i am try to to something different for login page"/>
          <Project title="Project 7" src={projectseven} words="In this project i am try to to something different for login page"/>
          <div
            className="flex justify-center text-center items-center p-10 bg-blue
              max-w-[400px] max-h-[400px] text-2xl font-playfair font-semibold"
          >
            SMOOTH USER EXPERIENCE
          </div>
        </motion.div>
      </div>
    </section>
  );
};

export default Projects;
